import React, {Component} from "react"
import './RightBar.css'
import Education from "./Education"
import Skills from "./Skills"
import Projects from './Projects';
import Thesis from "./Thesis";
import WorkHistory from './WorkHistory';

class RightView extends Component{

    render(){
        
        if (this.props.currentState == "About") {
                return(
                <div className = "grid-container mb-5">
                <div className = "align-items-lg-center ml-5">
                <text className = "display-1 d-block text-left align-self-center " id = "hi">HI, I am Sakib</text>
                <text className = "display-3 d-block text-left align-self-center " id = "hi">I am a Data Science Enthusiast.</text>
                <text className = "display-3 d-block text-left align-self-center " id = "hi">I also develop Websites</text>
                
                </div>
                </div> 
            );
        }
        if (this.props.currentState == "Education") {
                return(
                <div className = "grid-container">
                <Education></Education>
                </div> 
            );
        }
        if (this.props.currentState == "WorkHistory"){
            return(
            <div className = "grid-container">
            <WorkHistory></WorkHistory>
            </div> 
        );
        }
        if (this.props.currentState == "Skills") {
            return(
            <div className = "grid-container">
            <Skills></Skills>
            </div> 
        );
        }
        if (this.props.currentState == "Thesis") {
            return(
            <div className = "grid-container">
            <Thesis></Thesis>
            </div> 
        );
        }
        if (this.props.currentState == "Projects"){
            return(
            <div className = "grid-container">
            <Projects></Projects>
            </div> 
        );
        }
    }
}

export default RightView;